.timeline-row {
  .timeline-wrapper {
    position: relative;
    margin: 40px auto;
    width: 80%;
    min-height: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .timeline-bar {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    width: 3px;
    height: 100%;
    background: #0090ff;
    z-index: 0;
  }
  .timeline-present {
    position: absolute;
    top: -32px;
    left: 50%;
    transform: translateX(-50%);
    background: #0090ff;
    color: #fff;
    padding: 8px 20px;
    border-radius: 20px;
    font-weight: 700;
    font-size: 1.1rem;
    box-shadow: 0 2px 8px rgba(0,0,0,0.08);
    z-index: 3;
  }
  .timeline-row {
    display: flex;
    align-items: center;
    margin: 32px 0;
    position: relative;
    width: 50%;
    justify-content: flex-start;
  }
  .timeline-point-center {
    position: relative;
    width: 60px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    z-index: 2;
  }
  .timeline-point {
    width: 24px;
    height: 24px;
    background: #111;
    border-radius: 50%;
    border: 4px solid #0090ff;
    box-shadow: 0 0 0 2px #111;
  }
  .timeline-header-text {
    font-size: 1.3rem;
    font-weight: 700;
    color: #0090ff;
    margin-left: 24px;
    background: none;
    border-radius: 0;
    box-shadow: none;
    letter-spacing: 1px;
  }
}

/* Ensure .App fills the viewport and background */
.App {
  min-height: 100vh;
  width: 100%;
  max-width: 100vw;
  box-sizing: border-box;
  overflow-x: hidden;
}
body, .App {
  font-family: 'Fira Mono', 'Menlo', 'Monaco', 'Consolas', monospace;
  letter-spacing: 0.05em;
  word-spacing: 0.2em;
  margin: 0;
  padding: 0;
  width: 100%;
  max-width: 100vw;
  overflow-x: hidden;
}
/* Theme backgrounds for .App */
.App.light {
  background-color: #fff;
  color: #222;
}

.App.dark {
  background-color: #111;
  color: #eee;
}
/* Ensure all text is white in dark mode */
.App.dark h2,
.App.dark p,
.App.dark,
.App.dark .content,
.App.dark .top-menu button,
.App.dark .bottom-menu button {
  color: #fff !important;
}
/* Selected (active) button in top menu - dark theme */
.App.dark .top-menu button.active {
  color: #00ff41 !important; /* terminal green */
}
/* Top menu bar styles */
.top-menu {
  position: fixed;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  display: flex;
  gap: 2rem;
  background: rgba(255,255,255,0.85);
  border-radius: 24px 24px 24px 24px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.08);
  margin-top: 60px; /* Adds space above the menu */
  padding: 0.5rem 2rem;
  z-index: 100;
  font-family: 'Fira Mono', 'Menlo', 'Monaco', 'Consolas', monospace;
}
.App.dark .top-menu {
  background: rgba(30,30,30,0.95);
  box-shadow: 0 2px 16px rgba(0,0,0,0.18);
}
.top-menu button {
  background: none;
  border: none;
  font-size: 1.1rem;
  color: #222;
  padding: 0.5rem 1.5rem;
  border-radius: 16px;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
  font-family: inherit;
}
/* Selected (active) button in top menu - light theme */
.top-menu button.active {
  color: #a06be0 !important;
}
.App.dark .bottom-menu button {
  color: #eee;
}
.bottom-menu button.active {
  background: #007bff;
  color: #fff;
}
.section-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  min-height: 60vh;
  animation: fadeIn 0.3s;
}
  /* Secondary text color for themes */
  .App.light .secondary-text {
    color: #a06be0;
  }
  .App.dark .secondary-text {
    color: #00ff41;
  }
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}
.theme-toggle {
  width: 100%;
  display: flex;
  margin-top: 2px;
  justify-content: flex-end;
}

.switch {
  position: relative;
  display: inline-block;
  width: 56px;
  height: 32px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--slider-bg, #e0e0e0);
  transition: background 0.3s;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 6px;
}

.slider .icon {
  z-index: 2;
  transition: opacity 0.3s;
}
.slider .icon.left {
  opacity: 1;
}
.slider .icon.right {
  opacity: 0.5;
}

.switch input:checked + .slider {
  background: var(--slider-bg-dark, #222);
}
.switch input:checked + .slider .icon.left {
  opacity: 0.5;
}
.switch input:checked + .slider .icon.right {
  opacity: 1;
}

.slider:before {
  content: "";
  position: absolute;
  left: 4px;
  top: 4px;
  width: 24px;
  height: 24px;
  background: #fff;
  border-radius: 50%;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  transition: transform 0.3s;
}
.switch input:checked + .slider:before {
  transform: translateX(24px);
  background: #222;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--slider-bg, #e0e0e0);
  border-radius: 32px;
  transition: background 0.3s;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 6px;
}

.knob {
  position: absolute;
  top: 4px;
  left: 4px;
  width: 24px;
  height: 24px;
  background: #fff;
  border-radius: 50%;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s, background 0.3s;
  z-index: 2;
  color: #222;
}
.switch input:checked + .slider {
  background: var(--slider-bg-dark, #222);
}
.switch input:checked + .slider .knob {
  transform: translateX(24px);
  background: #222;
  color: #fff;
}

.content {
  max-width: 900px;
  margin: 6rem auto 0 auto;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 2px 16px rgba(0,0,0,0.04);
}

h1, h2, p {
  font-family: 'Fira Mono', 'Menlo', 'Monaco', 'Consolas', monospace;
  letter-spacing: 0.05em;
  word-spacing: 0.2em;
}
h1, h2 {
  font-weight: 600;
  margin-bottom: 1.5rem;
  letter-spacing: 0.1em;
}
p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  line-height: 1.7;
}

/* Mobile menu styles */
.menu-buttons {
  display: flex;
  gap: 2rem;
}

.mobile-menu {
  display: none;
  position: relative;
}

.dropdown-toggle {
  background: none;
  border: none;
  font-size: 1.1rem;
  color: #222;
  padding: 0.5rem 1.5rem;
  border-radius: 16px;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
  font-family: inherit;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.App.dark .dropdown-toggle {
  color: #fff;
}

.dropdown-arrow {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.13s cubic-bezier(0.4, 0.2, 0.2, 1);
}

.dropdown-arrow.open {
  transform: rotate(90deg);
}

.dropdown-arrow.closed {
  transform: rotate(0deg);
}

.dropdown-list {
  position: absolute;
  top: 100%;
  left: 1.6rem;
  background: rgba(255,255,255,0.95);
  border-radius: 16px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.08);
  list-style: none;
  padding: 0;
  margin: 0;
  min-width: 120px;
  z-index: 101;
}

.App.dark .dropdown-list {
  background: rgba(30,30,30,0.95);
  box-shadow: 0 2px 16px rgba(0,0,0,0.18);
}

.dropdown-list li {
  padding: 0.5rem 1.5rem;
  cursor: pointer;
  transition: background 0.2s;
}

.dropdown-list li:hover {
  background: rgba(0,0,0,0.05);
}

.App.dark .dropdown-list li:hover {
  background: rgba(255,255,255,0.05);
}

.dropdown-list li.active {
  color: #a06be0;
}

.App.dark .dropdown-list li.active {
  color: #00ff41;
}

/* Media queries for mobile */
@media (max-width: 768px) {
  .top-menu {
    gap: 1rem;
    padding: 0.5rem 1rem;
    box-shadow: none;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1200px;
  }

  .App.dark .top-menu {
    background: #111;
  }

  .menu-buttons {
    display: none;
  }

  .mobile-menu {
    display: block;
    margin-left: 1rem;
  }

  .theme-toggle {
    margin-top: 0;
    margin-right: 2rem;
  }
}
