/* PingPong Game Styles */

.ping-pong-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 1em;
}

.ping-pong-game {
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  width: 400px;
  height: 225px;
}

.ping-pong-game.light {
  border: 1px solid #111;
}

.ping-pong-game.dark {
  border: 1px solid #fff;
}

.ping-pong-canvas {
  border: 2px solid;
  border-radius: 8px;
  display: block;
}

.ping-pong-canvas.light {
  background-color: #fff;
  border-color: #ccc;
}

.ping-pong-canvas.dark {
  background-color: #111;
  border-color: #333;
}

/* Mobile specific styles */
@media (max-width: 768px) {
  .ping-pong-container {
    align-items: center;
  }
  
  .ping-pong-game {
    width: 280px;
    height: 158px;
  }
  
  .ping-pong-canvas {
    width: 280px !important;
    height: 158px !important;
  }
  
  .ping-pong-finished {
    width: 280px;
    height: 158px;
  }
  
  .ping-pong-hint {
    width: 280px;
    text-align: center;
    font-size: 14px;
  }
}

.ping-pong-finished {
  position: relative;
  width: 400px;
  height: 225px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 12px;
  font-size: 24px;
  font-family: monospace;
}

.ping-pong-finished.light {
  border: 1px solid #111;
  background-color: #fff;
  color: #111;
}

.ping-pong-finished.dark {
  border: 1px solid #fff;
  background-color: #111;
  color: #fff;
}

.ping-pong-hint {
  text-align: center;
  margin-top: 10px;
  font-size: 16px;
  font-family: monospace;
  transition: opacity 0.3s ease-in-out;
  min-height: 20px; /* Prevent layout shift */
  width: 100%;
  display: block;
}

.ping-pong-hint.show {
  opacity: 1;
}

.ping-pong-hint.hide {
  opacity: 0;
}

.ping-pong-hint.light {
  color: #666;
}

.ping-pong-hint.dark {
  color: #999;
}